import interfaces from '../../utils/interfaces.js'
export default {
	data() {
		return {
			goodsList: [], // 商品列表大数组
			filterby: "all", // 默认选择的tab
			page: 1,
			size: 6,
			loadingText: "正在加载....",
			filterByList: [{
					text: '全部',
					selected: true,
					filterby: 'all'
				},
				{
					text: '口碑',
					selected: false,
					filterby: 'public'
				},
				{
					text: '热门',
					selected: false,
					filterby: 'hot'
				}
			]
		}
	},
	onLoad(option) {
		// console.log(option)
		uni.setNavigationBarTitle({
			title: option.name
		})
		this.loadData()
	},
	//下拉刷新
	onPullDownRefresh() {
		setTimeout(() => {
			this.page = 1
			this.loadingText = '加载中...'
			this.goodsList = []
			this.loadData()
			uni.stopPullDownRefresh()
		}, 1000)
	},
	//上拉加载
	onReachBottom() {
		this.page++
		this.loadData()
	},
	methods: {
		handleGoods(item) {
			// 页面跳转 商品详情
			uni.navigateTo({
				url: './goods?goodsInfo=' + JSON.stringify(item)
			})
		},
		handleTarget(index) {
			this.filterByList[index].selected = true
			for (let k in this.filterByList) {
				if (k != index) this.filterByList[k].selected = false
			}
			//切换加载数据
			this.filterby = this.filterByList[index].filterby
			this.page = 1
			this.loadingText = '加载中...'
			this.goodsList = []
			this.loadData()
		},
		loadData() {
			this.request({
				url: `${interfaces.getGoodsList}/${this.filterby}/${this.page}/${this.size}`,
				// loading: this.loadingText,
				success: (res => {
					// console.log(res)
					// this.goodsList = res.data
					if (res.data.length > 0) {
						res.data.forEach(item => this.goodsList.push(item))
					} else {
						this.loadingText = '~我是有底线的~'
					}
				})
			})
		}
	}
}
